/* css variables */

:root {
  --clr-willies-light: #ec1d25;
  --clr-willies-dark: #0047ab;
}

/* Main Stylying */

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  background-color: #f8f1e8;
}

/* ------------------------------------ */
a {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}

a:focus {
  outline: none !important;
}

a:hover {
  text-decoration: none;
  color: #ec1d25;
  color: var(--clr-willies-light);
}

/* ------------------------------------ */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
}

p {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
}

ul,
li {
  margin: 0px;
  list-style-type: none;
}

/* ------------------------------------ */
input {
  outline: none;
  border: none !important;
}

textarea {
  outline: none;
}

textarea:focus,
input:focus {
  border-color: transparent !important;
  box-shadow: 0 0 0px 2px #ec1d25;
  box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -moz-box-shadow: 0 0 0px 2px #ec1d25;
  -moz-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -webkit-box-shadow: 0 0 0px 2px #ec1d25;
  -webkit-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -o-box-shadow: 0 0 0px 2px #ec1d25;
  -o-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -ms-box-shadow: 0 0 0px 2px #ec1d25;
  -ms-box-shadow: 0 0 0px 2px var(--clr-willies-light);
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-moz-placeholder {
  color: transparent;
}
input:focus::-moz-placeholder {
  color: transparent;
}
input:focus:-ms-input-placeholder {
  color: transparent;
}

textarea:focus::-webkit-input-placeholder {
  color: transparent;
}
textarea:focus:-moz-placeholder {
  color: transparent;
}
textarea:focus::-moz-placeholder {
  color: transparent;
}
textarea:focus:-ms-input-placeholder {
  color: transparent;
}

input::-webkit-input-placeholder {
  color: #aaaaaa;
}
input:-moz-placeholder {
  color: #aaaaaa;
}
input::-moz-placeholder {
  color: #aaaaaa;
}
input:-ms-input-placeholder {
  color: #aaaaaa;
}

textarea::-webkit-input-placeholder {
  color: #aaaaaa;
}
textarea:-moz-placeholder {
  color: #aaaaaa;
}
textarea::-moz-placeholder {
  color: #aaaaaa;
}
textarea:-ms-input-placeholder {
  color: #aaaaaa;
}

/* ------------------------------------ */
button {
  outline: none !important;
  border: none;
  background: transparent;
}

button:hover {
  cursor: pointer;
}

iframe {
  border: none !important;
}

/* ------------------------------------ */
.container {
  max-width: 1200px;
}

.slick-slide {
  outline: none !important;
}

/* page loading */

.animsition-loading-1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cp-spinner {
  width: 40px;
  height: 40px;
  display: inline-block;
  box-sizing: border-box;
  position: relative;
}

.cp-meter {
  border-radius: 50%;
  border-top: solid 6px #ec1d25;
  border-top: solid 6px var(--clr-willies-light);
  border-right: solid 6px #ec1d25;
  border-right: solid 6px var(--clr-willies-light);
  border-bottom: solid 6px #ec1d25;
  border-bottom: solid 6px var(--clr-willies-light);
  border-left: solid 6px #ec1d25;
  border-left: solid 6px var(--clr-willies-light);
  width: 48px;
  height: 48px;
  display: inline-block;
  box-sizing: border-box;
}

.cp-meter:before {
  border-radius: 3px;
  content: " ";
  width: 6px;
  height: 12px;
  display: inline-block;
  box-sizing: border-box;
  background-color: #ec1d25;
  background-color: var(--clr-willies-light);
  position: absolute;
  top: 5px;
  left: 16px;
  transform-origin: center bottom;
  -webkit-animation: cp-meter-animate-before 1s linear infinite;
          animation: cp-meter-animate-before 1s linear infinite;
}

@-webkit-keyframes cp-meter-animate-before {
  0% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(315deg);
  }
}

@keyframes cp-meter-animate-before {
  0% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(315deg);
  }
}

/* Back to the Top */

.btn-back-to-top {
  display: none;
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 40px;
  right: 40px;
  background-color: black;
  opacity: 0.5;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  border-radius: 4px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}

.symbol-btn-back-to-top {
  font-size: 22px;
  color: white;
  line-height: 1em;
}

.btn-back-to-top:hover {
  opacity: 1;
  cursor: pointer;
}

@media (max-width: 576px) {
  .btn-back-to-top {
    bottom: 15px;
    right: 15px;
  }
}

