/* css variables */

:root {
  --clr-willies-light: #ec1d25;
  --clr-willies-dark: #0047ab;
}

/* Main Stylying */

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  background-color: #f8f1e8;
}

/* ------------------------------------ */
a {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}

a:focus {
  outline: none !important;
}

a:hover {
  text-decoration: none;
  color: var(--clr-willies-light);
}

/* ------------------------------------ */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
}

p {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
}

ul,
li {
  margin: 0px;
  list-style-type: none;
}

/* ------------------------------------ */
input {
  outline: none;
  border: none !important;
}

textarea {
  outline: none;
}

textarea:focus,
input:focus {
  border-color: transparent !important;
  box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -moz-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -webkit-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -o-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -ms-box-shadow: 0 0 0px 2px var(--clr-willies-light);
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-moz-placeholder {
  color: transparent;
}
input:focus::-moz-placeholder {
  color: transparent;
}
input:focus:-ms-input-placeholder {
  color: transparent;
}

textarea:focus::-webkit-input-placeholder {
  color: transparent;
}
textarea:focus:-moz-placeholder {
  color: transparent;
}
textarea:focus::-moz-placeholder {
  color: transparent;
}
textarea:focus:-ms-input-placeholder {
  color: transparent;
}

input::-webkit-input-placeholder {
  color: #aaaaaa;
}
input:-moz-placeholder {
  color: #aaaaaa;
}
input::-moz-placeholder {
  color: #aaaaaa;
}
input:-ms-input-placeholder {
  color: #aaaaaa;
}

textarea::-webkit-input-placeholder {
  color: #aaaaaa;
}
textarea:-moz-placeholder {
  color: #aaaaaa;
}
textarea::-moz-placeholder {
  color: #aaaaaa;
}
textarea:-ms-input-placeholder {
  color: #aaaaaa;
}

/* ------------------------------------ */
button {
  outline: none !important;
  border: none;
  background: transparent;
}

button:hover {
  cursor: pointer;
}

iframe {
  border: none !important;
}

/* ------------------------------------ */
.container {
  max-width: 1200px;
}

.slick-slide {
  outline: none !important;
}

/* page loading */

.animsition-loading-1 {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.cp-spinner {
  width: 40px;
  height: 40px;
  display: inline-block;
  box-sizing: border-box;
  position: relative;
}

.cp-meter {
  border-radius: 50%;
  border-top: solid 6px var(--clr-willies-light);
  border-right: solid 6px var(--clr-willies-light);
  border-bottom: solid 6px var(--clr-willies-light);
  border-left: solid 6px var(--clr-willies-light);
  width: 48px;
  height: 48px;
  display: inline-block;
  box-sizing: border-box;
}

.cp-meter:before {
  border-radius: 3px;
  content: " ";
  width: 6px;
  height: 12px;
  display: inline-block;
  box-sizing: border-box;
  background-color: var(--clr-willies-light);
  position: absolute;
  top: 5px;
  left: 16px;
  transform-origin: center bottom;
  animation: cp-meter-animate-before 1s linear infinite;
}

@keyframes cp-meter-animate-before {
  0% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(315deg);
  }
}

/* Back to the Top */

.btn-back-to-top {
  display: none;
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 40px;
  right: 40px;
  background-color: black;
  opacity: 0.5;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  border-radius: 4px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}

.symbol-btn-back-to-top {
  font-size: 22px;
  color: white;
  line-height: 1em;
}

.btn-back-to-top:hover {
  opacity: 1;
  cursor: pointer;
}

@media (max-width: 576px) {
  .btn-back-to-top {
    bottom: 15px;
    right: 15px;
  }
}

/* Select2 */

/*[ width select ]
===========================================================*/
.select2-container {
  display: block;
  max-width: 100%;
  width: auto !important;
}

/*[ Select ]
===========================================================*/
.select2-container .select2-selection--single {
  display: flex;
  align-items: center;
  background-color: white;
  border: 0px solid transparent;
  border-radius: 10px !important;
  height: 46px;
  outline: none;
}

.select2-container--focus {
  border-radius: 10px;
  box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -moz-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -webkit-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -o-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -ms-box-shadow: 0 0 0px 2px var(--clr-willies-light);
}

.select2-container--focus .select2-selection--single {
  border-color: transparent;
}

.select2-container--below.select2-container--open {
  border-radius: 10px;
  box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -moz-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -webkit-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -o-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -ms-box-shadow: 0 0 0px 2px var(--clr-willies-light);
}

.select2-container--below.select2-container--open .select2-selection--single {
  border-color: transparent;
}

.select2-container--above.select2-container--open {
  border-radius: 10px;
  box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -moz-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -webkit-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -o-box-shadow: 0 0 0px 2px var(--clr-willies-light);
  -ms-box-shadow: 0 0 0px 2px var(--clr-willies-light);
}

.select2-container--above.select2-container--open .select2-selection--single {
  border-color: transparent;
}

/*[ in select ]
===========================================================*/
.select2-selection__rendered {
  font-size: 14px;
  font-family: Montserrat;
  font-weight: 400;
  color: #666666 !important;
  padding-left: 20px !important;
}

.select2-selection__arrow {
  top: 50% !important;
  transform: translateY(-50%) !important;
  right: 12px !important;
}

/*[ dropdown option ]
===========================================================*/
.select2-dropdown {
  border: none !important;
  border-radius: 10px !important;
  overflow: hidden;
  box-shadow: 0px 3px 25px rgba(0, 0, 0, 0.1);
  margin-top: 7px;
}
.select2-dropdown--above {
  top: -2px;
}
.select2-dropdown--below {
  top: 2px;
}

.select2-container--default .select2-results__option[aria-selected="true"],
.select2-container .select2-results__option--highlighted[aria-selected] {
  background-color: var(--clr-willies-light);
  color: #fff;
}

.select2-results__options {
  font-size: 14px;
  font-family: Montserrat;
  font-weight: 400;
  color: #666666 !important;
}

.select2-search--dropdown .select2-search__field {
  border: 1px solid #aaa !important;
  outline: none;
  font-family: Montserrat;
  font-weight: 400;
  font-size: 14px;
  color: #666666;
}

.select2-search--dropdown .select2-search__field:focus {
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  -o-box-shadow: none;
  -ms-box-shadow: none;
}

/* Header  */

